import React, { createContext, useContext, useState } from 'react';
const en = require('./en.json');
const vn = require('./vn.json');


type Language = 'en' | 'vn';

interface LanguageContextType {
    language: Language;
    setLanguage: (lang: Language) => void;
    t: (key: string) => string;
}

const translations: Record<Language, any> = { en, vn };

const LanguageContext = createContext<LanguageContextType | undefined>(undefined);

export const LanguageProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [language, setLanguageState] = useState<Language>(() => {
        const isBrowser = typeof window !== "undefined";
        const saved = isBrowser ? localStorage.getItem('app_language') : null;
        return (saved === 'vn' || saved === 'en') ? saved : 'en';
    });

    const setLanguage = (lang: Language) => {
        setLanguageState(lang);
        localStorage.setItem('app_language', lang);
        document.body.classList.remove('lang-en', 'lang-vn');
        document.body.classList.add(`lang-${lang}`);

        // Sync with backend
        import('../services/api').then(api => {
            api.updateUserLocale(lang);
        }).catch(err => console.error('Failed to sync locale:', err));
    };

    // Apply language class on initial load
    React.useEffect(() => {
        document.body.classList.add(`lang-${language}`);
    }, []);

    const t = (key: string): string => {
        const keys = key.split('.');
        let value = translations[language];

        for (const k of keys) {
            if (value && value[k]) {
                value = value[k];
            } else {
                return key; // Fallback to key if not found
            }
        }

        return typeof value === 'string' ? value : key;
    };

    return (
        <LanguageContext.Provider value={{ language, setLanguage, t }}>
            {children}
        </LanguageContext.Provider>
    );
};

export const useLanguage = () => {
    const context = useContext(LanguageContext);
    if (!context) {
        throw new Error('useLanguage must be used within a LanguageProvider');
    }
    return context;
};
